
.container {
  float: right;
  width: 800px;
  height: 720px;
  margin-top: 50px;
  -webkit-box-shadow: 2px 2px 10px 5px #d5ffd5;
          box-shadow: 2px 2px 10px 5px #d5ffd5;
  background: #fff;
}
h3 {
  width: 700px;
  margin: 0 auto;
  color: #308f49;
  padding-top: 30px;
  padding-left: 63px;
  font-weight: normal;
  font-size: 25px;
  border-bottom: 1px solid #308f49;
}
h3 .star {
    padding-right: 20px;
    width: auto;
    height: 20px;
}
.collectionList {
  position: relative;
  width: 700px;
  height: 600px;
}
.collection_empty {
  position: absolute;
  top: 30%;
  left: 50%;
  text-align: center;
  color: #308f49;
  opacity: .7;
}
.collection_empty svg {
    width: auto;
    height: 150px;
}
.collection_empty span {
    display: block;
    font-size: 20px;
}



