
.container {
  float: right;
  width: 800px;
  height: 720px;
  margin-top: 50px;
  -webkit-box-shadow: 2px 2px 10px 5px #d5ffd5;
          box-shadow: 2px 2px 10px 5px #d5ffd5;
  background: #fff;
}
h3 {
  width: 700px;
  margin: 0 auto;
  color: #308f49;
  padding-top: 30px;
  padding-left: 63px;
  font-weight: normal;
  font-size: 25px;
  border-bottom: 1px solid #308f49;
}
h3 .money {
    padding-right: 20px;
    width: auto;
    height: 20px;
}
.intergral_slider {
  float: left;
  margin-left: 40px;
  margin-top: 40px;
}
.intergral_slider .currentIntergral {
    font-size: 20px;
}
.intergral_slider .intergral_nav {
    height: 450px;
    margin-top: 20px;
    padding-top: 30px;
    padding-right: 30px;
    border-right: 1px solid #ccc;
    font-size: 18px;
}
.intergral_slider .intergral_nav li {
      padding: 20px;
      cursor: pointer;
}
.intergral_main {
  float: right;
  width: 560px;
  height: 500px;
  margin-top: 60px;
}
.router-link-active {
  color: #308f49;
}



