
.container {
  float: right;
  width: 800px;
  height: 720px;
  margin-top: 50px;
  -webkit-box-shadow: 2px 2px 10px 5px #d5ffd5;
          box-shadow: 2px 2px 10px 5px #d5ffd5;
  background: #fff;
}
h3 {
  width: 700px;
  margin: 0 auto;
  color: #308f49;
  padding-top: 30px;
  padding-left: 63px;
  font-weight: normal;
  font-size: 25px;
  border-bottom: 1px solid #308f49;
}
h3 .camera {
    padding-right: 20px;
    width: auto;
    height: 20px;
}
.info-form {
  padding: 20px;
  color: #656766;
}
.info-form li {
    margin-bottom: 40px;
}
.info-form li span {
      color: #9a9a9a;
}
.info-form li input {
      border: 1px solid #999;
}
.info-form li button {
      margin-left: 20px;
      font-size: 12px;
      background: #fff;
}
.info-form li .btn-enterroom {
      padding: 1px 7px;
      margin-left: 30px;
      color: #5cc591;
      border: 1px solid #5cc591;
      border-radius: 10px;
}
.info-form li .btn-modify {
      padding: 1px 7px;
      color: #bbb9b9;
      border: 1px solid #bbb9b9;
      border-radius: 10px;
}
.info-form li .createRoom {
      display: inline-block;
      width: 95px;
      height: 35px;
      margin-left: 116px;
      color: #fff;
      line-height: 2;
      text-align: center;
      border-radius: 5px;
      background: #396;
      cursor: pointer;
}
.info-form li .createRoom:hover {
        opacity: .8;
}



